.form-control-square{display:block;width:100%;height:40px;font-size:1rem;line-height:1.5;color:#495057;background-color:#fff;background-clip:padding-box;border:1px solid #ced4da;border-radius:.25rem;transition:border-color .15s ease-in-out,box-shadow .15s ease-in-out;opacity:1!important}.form-control-square::-ms-expand{background-color:transparent;border:0}.form-control-square:focus{color:#495057;background-color:#fff;border-color:#80bdff;outline:0;box-shadow:0 0 0 .2rem rgba(0,123,255,.25)}.input-group.is-invalid~.invalid-feedback{display:block}